<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="template" ref="kalixBizDialog" v-bind:formModel.sync="formModel" v-bind:targetURL="targetURL")
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="name" v-bind:label-width="labelWidth" v-bind:rules="rules.name")
        el-input(v-model="formModel.name")
      el-form-item(label="" prop="desc" v-bind:label-width="labelWidth")
        el-input(type="textarea" v-model="formModel.desc" v-bind:rows="3")
</template>

<script type="text/ecmascript-6">
  import FormModel from './templateModel'
  import {templateURL} from '../../config.toml'

  export default {
    name:'AdminTemplateAdd',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px',
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}]
        },
        targetURL: templateURL
      }
    },
    methods: {
    }
  }
</script>

<style scoped lang="stylus" type="text/stylus">

</style>



