<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="audit" ref="kalixBizDialog" v-bind:formModel.sync="formModel" isView)
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="appName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.appName" readonly)
      el-form-item(label="" prop="funName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.funName" readonly)
      el-form-item(label="" prop="actor" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.actor" readonly)
      el-form-item(label="" prop="action" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.action" readonly)
      el-form-item(label="" prop="content" v-bind:label-width="labelWidth")
        el-input(type="textarea" v-model="formModel.content" v-bind:rows="3" readonly)
</template>

<script type="text/ecmascript-6">
  import FormModel from './model'

  export default {
    name :'AdminAuditView',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px'
      }
    }
  }
</script>



