<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="application" ref="kalixBizDialog" v-bind:formModel.sync="formModel" isView)
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="name" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.name" readonly)
      el-form-item(label="" prop="code" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.code" readonly)
      el-form-item(label="" prop="iconCls" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.iconCls" readonly)
      el-form-item(label="" prop="remark" v-bind:label-width="labelWidth")
        el-input(type="textarea" v-model="formModel.remark" v-bind:rows="3" readonly)
</template>

<script type="text/ecmascript-6">
  import FormModel from './model'

  export default {
    name: 'AdminApplicationView',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px'
      }
    }
  }
</script>



