<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="templateContent" ref="kalixBizDialog" v-bind:formModel.sync="formModel" isView)
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="templateType" v-bind:rules="rules.templateType" v-bind:label-width="labelWidth")
        kalix-dict-select(v-model="formModel.templateType" appName="admin" dictType="" style="width:100%" disabled)
      el-form-item(label="" prop="content" v-bind:label-width="labelWidth")
        el-input(type="textarea" v-model="formModel.content" v-bind:rows="6" readonly)
</template>

<script type="text/ecmascript-6">
  import FormModel from './templateConfigModel'

  export default {
    name: 'AdminTemplateContentView',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px',
        rules: {
          templateType: [{required: true, message: '', trigger: 'change'}]
        }
      }
    }
  }
</script>

<style scoped lang="stylus" type="text/stylus">

</style>



