<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="templateConfig" ref="kalixBizDialog" v-bind:formModel.sync="formModel" v-bind:targetURL="targetURL")
    div.el-form(slot="dialogFormSlot")
      input(v-model="formModel.templateId" type="hidden")
      el-form-item(label="" prop="fieldName" v-bind:label-width="labelWidth" v-bind:rules="rules.fieldName")
        el-input(v-model="formModel.fieldName")
      el-form-item(label="" prop="fieldValue" v-bind:label-width="labelWidth" v-bind:rules="rules.fieldValue")
        el-input(v-model="formModel.fieldValue")
      el-form-item(label="" prop="fieldDesc" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.fieldDesc")
</template>

<script>
  import FormModel from './templateConfigModel'
  import {templateConfigURL} from '../../config.toml'

  export default {
    name:'AdminTemplateConfigEdit',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px',
        rules: {
          fieldName: [{required: true, message: '', trigger: 'blur'}],
          fieldValue: [{required: true, message: '', trigger: 'blur'}]
        },
        targetURL: templateConfigURL
      }
    }
  }
</script>

<style scoped>

</style>



