<template lang="pug">
  keep-alive
    kalix-table(bizKey="application" title="" v-bind:targetURL="targetURL"
    v-bind:bizDialog="bizDialog" v-bind:tableFields="tableFields" bizSearch="AdminApplicationSearch"
    v-bind:buttonPermissionPrefix="buttonPermissionPrefix" v-bind:btnList="btnList" v-bind:toolbarBtnList="toolbarBtnList"
    v-bind:customTableTool="customTableTool" v-bind:customRender="customRender" ref="applicationTable" )
</template>

<script type="text/ecmascript-6">
  import {
    applicationsURL,
    appsStartURL,
    appsStopURL,
    applicationBtnPermissionPrefix
  } from '../../config.toml'
  import {applicationBtnList} from './config'
  import {ON_REFRESH_DATA} from 'kalix-vue-common/src/components/common/event.toml'

  export default {
    name: 'kalix-admin-application',
    data() {
      return {
        btnList: applicationBtnList,
        buttonPermissionPrefix: applicationBtnPermissionPrefix,
        targetURL: applicationsURL,
        startURL: appsStartURL,
        stopURL: appsStopURL,
        toolbarBtnList: [
          {id: 'add', isShow: false}
        ],
        tableFields: [
          {prop: 'name', label: ''},
          {prop: 'code', label: ''},
          {prop: 'iconCls', label: ''},
          {prop: 'activeState', label: ''}
        ],
        bizDialog: [
          {id: 'view', dialog: 'AdminApplicationView'}
        ]
      }
    },
    methods: {
      customRender(_data) {
        _data.forEach(function (e) {
          e.activeState = e.active ? '' : ''
        })
      },
      customTableTool(row, btnId, that) {
        switch (btnId) {
          case 'startStopUsing': { // /
            let warnInfo = '' + (row.active === true ? '' : '') + ''
            that.$confirm(warnInfo, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              return this.axios.request({
                method: 'GET',
                url: (row.active === true ? this.stopURL : this.startURL),
                params: {
                  id: row.code
                },
                data: {
                  id: row.code,
                  available: row.active
                }
              })
            }).then((res) => {
              console.log(res)
              this.$KalixEventBus.$emit(ON_REFRESH_DATA)
            })
            break
          }
        }
      }
    }
  }
</script>

<style scoped lang="stylus" type="text/stylus">

</style>



