const unstartCond = (scope) => {
  return scope.row.code !== 'admin'
}
const applicationBtnList = [
  {
    id: 'view',
    title: '',
    isShow: true,   // 
    isPermission: true  // 
  },
  {
    id: 'startStopUsing',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: unstartCond,
    titleCompute: (scope) => {
      return scope.row.active === true ? '' : ''
    }
  }
]
export {applicationBtnList}



