<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="templateConfig" ref="kalixBizDialog" v-bind:formModel.sync="formModel" isView)
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="fieldName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.fieldName" readonly)
      el-form-item(label="" prop="fieldDesc" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.fieldValue" readonly)
      el-form-item(label="" prop="fieldDesc" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.fieldDesc" readonly)
</template>

<script type="text/ecmascript-6">
  import FormModel from './templateConfigModel'
  export default {
    name:'AdminTemplateConfigView',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px'
      }
    }
  }
</script>

<style scoped lang="stylus" type="text/stylus">

</style>



