<template lang="pug">
  kalix-dialog.user-add(title='' bizKey="auditconfig" ref="kalixBizDialog" v-bind:formModel.sync="formModel" isView)
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="clsName" v-bind:label-width="labelWidth" v-bind:rules="rules.clsName")
        el-input(v-model="formModel.clsName" readonly)
      el-form-item(label="" prop="appName" v-bind:label-width="labelWidth" v-bind:rules="rules.appName")
        el-input(v-model="formModel.appName" readonly)
      el-form-item(label="" prop="funName" v-bind:label-width="labelWidth" v-bind:rules="rules.funName")
        el-input(v-model="formModel.funName" readonly)
      el-form-item(label="" prop="enable" v-bind:label-width="labelWidth" v-bind:rules="rules.enable")
        el-select(v-model="formModel.enable" placeholder="" disabled)
          el-option(v-for="item in options" v-bind:key="item.value" v-bind:value="item.value" v-bind:label="item.label")
      el-form-item(label="" prop="remark" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.remark" type="textarea" v-bind:rows="3" readonly)
</template>

<script type="text/ecmascript-6">
  import FormModel from './model'

  export default {
    name: 'AuditConfigView',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        rules: {
          clsName: [{required: true, message: '', trigger: 'blur'}],
          appName: [{required: true, message: '', trigger: 'blur'}],
          funName: [{required: true, message: '', trigger: 'blur'}],
          enable: [{required: true, message: '', trigger: 'blur'}]
        },
        options: [{
          value: true,
          label: ''
        }, {
          value: false,
          label: ''
        }],
        labelWidth: '120px'
      }
    }
  }
</script>

<style scoped lang="stylus" type="text/stylus">

</style>



